<script>
import { mapGetters } from "vuex";
export default {
    computed: {
        ...mapGetters([
            "questionsListsLength",
            "rightAnswers",
            "finishContItem"
        ]),
        setbg() {
            return {
                background: `url(${this.finishContItem.background}) 0/cover no-repeat`
            };
        }
    },
    methods: {
        restart() {
            this.$store.commit("resetRightAnswers");
            this.$store.commit("resetPage");
            this.$store.commit("toTesting", "start");
        }
    }
};
</script>

<style lang="scss">
.testFinish {
    &__content {
        min-height: 536px;
        max-width: 500px;
        padding: 30px 20px 45px 20px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &__result {
        margin: 0 0 60px 0;
    }
    &__title {
        font-weight: 900;
        font-size: 36px;
        line-height: 42px;
        margin: 0 0 40px 0;
    }
    &__social {
        display: flex;
        flex-wrap: wrap;
    }
    &__social-item {
        height: 50px;
        min-width: 60px;
        background: #fff;
        margin: 0 15px 15px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 15px 22px rgba(121, 54, 68, 0.225628);
        border-radius: 2px;
        cursor: pointer;
        &:last-child {
            margin: 0;
        }
    }
    &__social-item--facebook {
        padding: 0 20px;
        span {
            margin: 0 0 0 12px;
        }
    }
    &__restart {
        font-weight: bold;
        font-size: 20px;
        line-height: 23px;
        background: none;
        position: relative;
        margin: auto 30px 0 0;
        outline: none;
        &::after {
            content: "";
            position: absolute;
            right: -30px;
            top: 50%;
            transform: translateY(-50%);
            display: inline-block;
            width: 21px;
            height: 21px;
            background-image: url("~@/assets/images/refresh-arrow.svg");
            background-repeat: no-repeat;
        }
    }
}
</style>